\n    &:focus\r\n      @extend .ui.theme-color.darker\r\n      @extend .ui.border.bright\r\n\r\n  &.focus-lighten\r\n    @extend .ui.transition\r\n    &:focus\r\n      @extend .ui.theme-color.light\r\n      @extend .ui.border.bright\r\n\r\n  &.fullscreen\r\n    position: fixed\r\n    left: 0\r\n    top: 0\r\n    right: 0\r\n    bottom: 0\r\n\r\n  &.overlay\r\n    @extend .ui.fullscreen\r\n\r\n    background-color: rgba(black, 0.3)\r\n    display: flex\r\n    justify-content: center\r\n    align-items: center\r\n\r\n  &.panel\r\n    @extend .ui.theme-color.main\r\n    @extend .ui.shadow\r\n\r\n    width: min-content\r\n    padding: 0em 1.5em\r\n    text-align: center\r\n\r\n  &.side-panel\r\n    @extend .ui.panel\r\n    position: absolute\r\n    top: 0\r\n    bottom: 0\r\n    width: 12em\r\n    padding: 0\r\n    text-align: left\r\n    overflow-y: auto\r\n\r\n    &.left { left: 0 }\r\n    &.right { right: 0 }\r\n\r\n  &.section\r\n    @extend .ui.theme-color.dark\r\n    @extend .ui.border\r\n    @extend .ui.transition\r\n    @extend .ui.focus-darken\r\n    width: 10rem\r\n    padding: 0.4em 0.6em\r\n    display: block\r\n\r\n  &.section.icon\r\n    position: relative\r\n\r\n    .fa\r\n      opacity: 0.5\r\n      position: absolute\r\n      top: 0.6em\r\n      pointer-events: none\r\n\r\n    &.left .fa { left: 0.6em }\r\n    &.right .fa { right: 0.6em }\r\n\r\n  &.text-input\r\n    display: inline-block\r\n    width: max-content\r\n\r\n    input, [contenteditable]\r\n      @extend .ui.section\r\n\r\n    &.icon\r\n      @extend .ui.section.icon\r\n\r\n      &.left\r\n        input, [contenteditable] { padding-left: 1.8em }\r\n\r\n      &.right\r\n        input, [contenteditable] { padding-right: 1.8em }\r\n\r\n  &.button\r\n    @extend .ui.theme-color.dark\r\n    @extend .ui.border\r\n    @extend .ui.hover-darken\r\n    @extend .ui.focus-darken\r\n    padding: 0.4em 1.5em\r\n    user-select: none\r\n\r\n  &.form &.field\r\n    margin-bottom: 1em\r\n\r\n  &.selection\r\n    @extend .ui.theme-color.dark\r\n    @extend .ui.border\r\n    @extend .ui.noselect\r\n\r\n    width: 14em\r\n    height: 18em\r\n    list-style: none\r\n    overflow-y: scroll\r\n    display: inline-block\r\n\r\n    li\r\n      @extend .ui.hover-darken\r\n      display: block\r\n      padding: 0.5em\r\n\r\n      &:hover\r\n        border-bottom: none\r\n\r\n      &.active\r\n        $active-color = rgb(39, 174, 96)\r\n        background-color: rgba($active-color, 0.3)\r\n        color: lighten($active-color, 20%)\r\n\r\n  &.pull.right\r\n    float: right\r\n\r\n  &.scroll\r\n    overflow-y: auto\r\n\r\n  &.link\r\n    @extend .ui.transition\r\n    text-decoration: none\r\n    border-bottom: 1px solid rgba($text-color, 0.3)\r\n    position: relative\r\n\r\n    &:hover\r\n      border-bottom-color: rgba($text-color, 0.8)\r\n\r\n  &.divider\r\n    border-bottom: 1px solid rgba($text-color, 0.2)\r\n",".ui.transition,\n.ui.hover-darken,\n.ui.focus-darken,\n.ui.focus-lighten,\n.ui.section,\n.ui.text-input input,\n.ui.text-input [contenteditable],\n.ui.button,\n.ui.selection li,\n.ui.link {\n  transition: 0.2s ease all;\n}\n.ui.noselect,\n.ui.selection {\n  user-select: none;\n}\n.ui.small {\n  font-size: 75%;\n}\n.ui.subtle {\n  opacity: 0.5;\n}\n.ui.shadow,\n.ui.panel,\n.ui.side-panel {\n  box-shadow: 0px 4px 12px rgba(0,0,0,0.2);\n}\n.ui.theme-color.main,\n.ui.panel,\n.ui.side-panel {\n  background-color: #264c73;\n}\n.ui.theme-color.dark,\n.ui.section,\n.ui.text-input input,\n.ui.text-input [contenteditable],\n.ui.button,\n.ui.selection {\n  background-color: #1d3a58;\n}\n.ui.theme-color.light,\n.ui.focus-lighten:focus {\n  background-color: #2f5e8e;\n}\n.ui.theme-color.darker,\n.ui.hover-darken:hover,\n.ui.focus-darken:focus,\n.ui.section:focus,\n.ui.text-input input:focus,\n.ui.text-input [contenteditable]:focus,\n.ui.button:hover,\n.ui.button:focus,\n.ui.selection li:hover {\n  background-color: #172e46;\n}\n.ui.theme-color.lighter {\n  background-color: #376ea